
@import url("https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css");
@import url("https://fonts.googleapis.com/css?family=Comfortaa:700");
@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,600,700,800");
.preview .media .media-content p {
  margin-bottom: 0.4rem;
}
.preview .media .media-content .platform, .preview .media .media-content .zone {
  color: #b0b0b0;
  font-size: 0.8rem;
}
.preview .media .media-content .platform .icon, .preview .media .media-content .zone .icon {
    margin-right: 0.4rem;
}
.preview .button {
  transition: 0.2s;
  margin: 1rem auto;
}
.preview table.table {
  margin-bottom: 1rem;
}
.preview .el-collapse .el-collapse-item__header {
  border-left: 4px solid #2DA7E6;
  padding-left: 7px;
  transition: 0.3s;
}
.preview .el-collapse .el-collapse-item__header .image-user {
    float: left;
    border-radius: 50%;
    height: 32px;
    margin-top: 5px;
    margin-right: 13px;
}
.preview .el-collapse .el-collapse-item__header .post-by {
    display: inline-block;
}
.preview .el-collapse .el-collapse-item__header:hover {
    border-left: 15px solid #2DA7E6;
}
.preview .el-collapse .el-collapse-item__header:hover i {
      color: #2DA7E6;
}
.preview .el-collapse .el-collapse-item__wrap .el-collapse-item__content {
  padding: 0px;
}
.preview .el-collapse .el-collapse-item__wrap .el-collapse-item__content table {
    margin: 0.4rem auto;
}
.preview .el-collapse .el-collapse-item__wrap .el-collapse-item__content table tr td {
      padding: 0.2rem;
      border-bottom: 0px;
}
.preview .el-collapse .el-collapse-item__wrap .el-collapse-item__content table tr td:first-child {
      width: 12%;
}
.preview .el-collapse .el-collapse-item__wrap .el-collapse-item__content table tr td:first-child .icon-user {
        margin-top: 5px;
        margin-left: 0.8rem;
}



