
@import url("https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css");
@import url("https://fonts.googleapis.com/css?family=Comfortaa:700");
@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,600,700,800");
.buy-preview {
  position: -webkit-sticky;
  position: sticky;
  top: 65px;
  height: 90vh;
  overflow-y: auto;
}
.buy-preview .media .media-left img {
    height: 80px;
    width: 80px;
}
.buy-preview .el-carousel {
    margin-top: 0.75rem;
    background-color: #F5F5F5;
}
.buy-preview .el-carousel img {
      max-height: 225px;
      position: absolute;
      margin: auto;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
}
.buy-preview hr {
    margin: 0.5rem auto;
}
.buy-preview .option .button {
    transition: 0.2s;
}
.buy-preview .option .button span {
      margin-left: 3px !important;
}
.buy-preview::-webkit-scrollbar {
  display: none;
}



